import React from 'react';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';

import {authenticationService} from "../services/authentication.service";

class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props);
        if(this.props.location.search) {
            const code = new URLSearchParams(this.props.location.search).get("code")
            console.log(code)
            authenticationService.loginByCode(code);
        }

        // redirect to home if already logged in
        if (authenticationService.currentUserValue) {
            this.props.history.push('/');
        }
    }

    implicitLogin() {
        window.location.replace('http://localhost:8085/auth/oauth/authorize?client_id=browser&response_type=code&redirect_uri=http://localhost:4000/login');
    }

    render() {
        return (
            <div>
                <div className="alert alert-info">
                    <strong>Normal User</strong> - U: VASYA P: 12345<br />
                    <strong>Administrator</strong> - U: admin P: admin
                </div>
                <h2>Login</h2>
                <Formik
                    initialValues={{
                        username: '',
                        password: ''
                    }}
                    validationSchema={Yup.object().shape({
                        username: Yup.string().required('Username is required'),
                        password: Yup.string().required('Password is required')
                    })}
                    onSubmit={({ username, password }, { setStatus, setSubmitting }) => {
                        setStatus();
                        authenticationService.login(username, password)
                            .then(
                                user => {
                                    const { from } = this.props.location.state || { from: { pathname: "/" } };
                                    this.props.history.push(from);
                                },
                                error => {
                                    setSubmitting(false);
                                    setStatus(error.toString());
                                }
                            );
                    }}
                    render={({ errors, status, touched, isSubmitting }) => (
                        <Form>
                            <div className="form-group">
                                <label htmlFor="username">Username</label>
                                <Field name="username" type="text" className={'form-control' + (errors.username && touched.username ? ' is-invalid' : '')} />
                                <ErrorMessage name="username" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <Field name="password" type="password" className={'form-control' + (errors.password && touched.password ? ' is-invalid' : '')} />
                                <ErrorMessage name="password" component="div" className="invalid-feedback" />
                            </div>
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary" disabled={isSubmitting}>Login</button>
                                {isSubmitting &&
                                <img alt="LOL" src="data:image/gif;base64," />
                                }
                            </div>
                            {status &&
                            <div className={'alert alert-danger'}>{status}</div>
                            }
                        </Form>
                    )}
                />
                <button className="btn btn-primary" onClick={this.implicitLogin}>Implicit Login</button>
            </div>
        )
    }
}

export { LoginPage };


